body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  letter-spacing: 0.4px;
}
ul{
  list-style-type: none;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/*----------Header----------*/
div.Header{
  position:fixed;
  margin:-100px 0;
  width:100%;
  z-index:30;
  div.categories{
    border:2px solid black;
    display:flex;
    justify-content:center;
    & > div{
      width:20%;
    }
  }
  nav{
    width:100vw;
    height:75px;
    margin:0;
    position: sticky;
    background:white;
    box-shadow:0px 2px 10px #e7e7e7;
    position: relative;
    z-index:30;
  }
  div.container{
    display:flex;
    justify-content: space-evenly !important;
    height:inherit;
    width:96vw;
  }
  div.logo{
    img{
      margin:10px;
    }
  }
  div.links{
    font-size:1em !important;
    width:600px;
    margin-top:25px;
    a{
      color:black;
      font-weight: bold;
      padding:28px 25px;
    }
    a[href="/men"]:hover{
      border-bottom:4px solid #ee5f73;
    }
    a[href="/women"]:hover{
      border-bottom:4px solid #fb56c1;
    }
    a[href="/kids"]:hover{
      border-bottom:4px solid #f26a10;
    }
    a[href="/hl"]:hover{
      border-bottom:4px solid #f2c210;
    }
    a[href="/ess"]:hover{
      border-bottom:4px solid #0db7af;
    }
  }
  div.search{
    text-align: center;
    width:450px;
    input{
      width:350px;
      margin-top:15px;
    }
    form{
      margin-top:10px;
    }
  }
  div.userInfo{
    width:200px;
    display:flex;
    height:inherit;
    div.profile{
      i{
        margin-left:4px;
      }
      span{
        margin-left:-3px;
      }
    }
    div{
      width:55px;
      height:100%;
      padding:10px;
      transition:all 0.2s ease;
      i{
        margin:6px auto;
      }
      span{
        width:fit-content;
        color:black;
        margin:auto -7px;
        font-weight: bold;
        font-size: smaller;
      }
    }
  }
  div.overlay{
    position: fixed;
    width:100vw;
    height:100vh;
    background:rgba(0,0,0,0.2);
    z-index:20;
  }
  div.menu{
    width:1150px;
    height:450px;
    position:fixed;
    z-index:25;
    background:white;
    margin-left:80px;
    transition:opacity 0.5s ease-out;
    h5{
      margin-bottom: 5px;
    }
    div.mens h5{
        color:#ee5f73;
    }
    div.womens h5{
      color:#fb56c1;
    }
    div.kids h5{
      color:#f26a10;
    }
    div.homeLiving h5{
      color:#f2c210;
    }
    div.essentials h5{
      color:#0db7af;
    }
    a{
      color:rgba(0, 0, 0,0.8);
    }
    hr{
      width:65%;
      border: 1px solid rgba(160, 160, 160,0.4);
    }
    & > div{
      height:inherit;
      display:flex;
      padding:5px;
      div{
        // border:2px solid black;
        width:20%;
        height:inherit;
        &:nth-child(even){
          background:rgb(241, 241, 241,0.35);
        }
      }
    }
    ul{
      li{
        padding:2.5px;
      }
    }
    ul li:hover{
      font-weight: bolder;
    }
  }
  div.profileMenu{
    width:340px;
    height:500px;
    position:fixed;
    left:74vw;
    z-index:25;
    padding:20px;
    background:white;
    transition:all 0.3s ease;
    box-shadow:0 2px 10px rgba(168, 168, 168, 0.6);
    a{
      color:rgb(48, 48, 48);
    }
    a.login{
      color:#ff3f6c;
      font-weight:bold;
      padding:12px;
      border:1px solid rgba(133, 133, 133, 0.2);
      border-radius:3px;
    }
    a.login:hover{
      border-color:red;
    }
    hr{
      margin-top:35px;
      width:100%;
      border: 1px solid rgba(160, 160, 160,0.4);
    }
    ul{
      margin-left:-40px;
      li{
        padding:2.5px;
      }
    }
    ul li:hover{
      font-weight: bolder;
    }
  }
}
/*----------Header----------*/

/*----------Footer----------*/
div.Footer{
  padding:15px;
  a{
    color:rgb(48, 48, 48);
  }
  background:#fafbfc;
  div.top{
    margin-top:30px;
    display:flex;
    justify-content: space-evenly;
  }
  li{
    padding:2.5px;
  }
  div.onMobile {
    img{
      width:160px;
      height:50px;
    }
    img.sns{
      width:35px;
      height:35px;
      margin:5px;
    }
  }
  div.extraInfo{
    width:250px;
    img{
      width:60px;
      height:60px;
    }
    div{
      width:100%;
      display:flex;
      div{
        width:140px;
      }
      div.img{
        width:70px;
      }
    }
  }
}
/*----------Footer----------*/

/*----------Home----------*/
div.Home{
  margin:100px 40px;
  h2{
    color:#3e4152;
    letter-spacing: 3px;
  }
  div.categories{
    display:flex;
    justify-content: space-evenly;
    & > div{
      width:17%;
      img{
        width:100%;
      }
    }
  }
  div.topBrands{
    display:flex;
    justify-content: space-evenly;
    div{
      width:17%;
      img{
        width:100%;
      }
    }
  }
  div.playAtHome{
    display:flex;
    justify-content: space-evenly;
    div{
      width:22%;
      img{
        width:100%;
      }
    }
  }
  div.fashionSuperstar{
    img{
      width:100%;
    }
  }
  div.diyAtHome{
    display:flex;
    width:90%;
    div{
      width:50%;
      img{
        width:100%;
      }
    }
    margin:auto;
  }
  div.colorEdit img{
    margin:auto;
    width:100%;
  }
  div.essStories{
    display:flex;
    justify-content: space-evenly;
    img{
      width:100%;
    }
  }
}
/*----------Home----------*/
